@import url('https://fonts.googleapis.com/css2?family=Inconsolata&display=swap');
.photoContainerTrue{
    width: 1500px;
    
   
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;



}
.draganddrop{
    width: 200px;
    height: 200px;
  
    border:1px solid black
}
.photo{

  
    display: grid;
    grid-template-columns: repeat(3, 3fr);
    padding:70px;
 
    


}
.alterVideo{
  margin-top: 30px;

}
.or{

    width: 200px;
    margin-bottom: 20px;
}

input[type="file"] {
    display: none;
  }

  .custom-file-upload {
    margin-top: 100px;
    margin-bottom: 20px;
    
    display:flex;
    justify-content: start;
    align-items: center;

    cursor: pointer;
  
    width:200px;
    height:  50px;
    opacity:1;
    transition: .3s ease;
    background-color: rgb(255, 255, 255);
   
    border: 1px solid black;
  }
  .custom-file-upload:hover {
    opacity: 0.70;
  }
  .addIcon{
    width: 20px;
    margin-left: 13px;
    padding-right: 13px;
   
    transition: .3s ease;
    
  }
  .addText{
   
    width: 50px;
    width: 100px;
    border-left: 1px solid black;
    padding-left: 30px;
    
    padding: 10px;
    font-family: 'Inconsolata', monospace;
  }

  .downIcon{
    width: 60px;
    margin-top: 80px;
  }
  .or{
    font-family: 'Inconsolata', monospace;
  }

  @media(max-width:1300px){
    .photoContainerTrue{
      width: 1400px;
  
    }
  }
  