.photoItem{
    
    width: 250px;
    height: 250px;
  
    background-size: cover;
}

.dropContainer{
    position: relative;
    cursor: grab;
 
    width: 250px;
    height: 250px;
    margin: 5px;
 
}
.deletePhoto{
    position: absolute;
    z-index: 1;
    right: 0;
    top: 10px;
}
.phoroContainer{
    position: absolute;
    z-index: 0;
    
}
.deleteIcon{
    width: 20px;
}
.deleteButton{
    border: none;
    background: none;
    cursor: pointer;
    display: none;
    transition: width 2s, height 4s;

}